import { atom } from 'recoil';
export type ListsQuestion = { id: number; question: string };
export type ListsSubject = { subject: string };
export type GroupAddStep = {
    therapy_grouping: string;
    participation_age: { startAge: number, endAge: number };
    therapy_cycle : { count_in_week : number, hours_each_therapy : number };    
    participation_questions : { question_responses : { id : number, question : string }[] }
    group_therapy_subject_response : { therapy_subject_responses : { subject : string }[] }
};
export const initGroupAddStep = {
    therapy_grouping: '',
    participation_age: { startAge: 0, endAge: 1 }, 
    therapy_cycle : { count_in_week : 1, hours_each_therapy : 1 },
    participation_questions : { question_responses : [ { id : 0, question : '' } ] },
    group_therapy_subject_response : { therapy_subject_responses : [ { subject : '' } ] },
}
export const initGroupAddStepAtom = atom<GroupAddStep>({
    key: 'initGroupAddStepAtom', 
    default: initGroupAddStep
});
export const sliderYear = atom({
    key: 'sliderYear',
    default: { startAge: 0, endAge: 1 }
});
export const sliderBasic = atom({
    key: 'sliderBasic',
    default: { checkBasic: 1 }
});
export const sliderTime = atom({
    key: 'sliderTime',
    default: { checkTime: 1 }
});
export type ListsArrayProps = { id: number; text: string };
export const initList = {
        id: 0,
        text: 'Init Title',
}
