import { InputBase } from '@mui/material';
import * as React from 'react';
import { BoxLabel } from '../../../components';
import { CertificateListArray } from './MyInfoType';
import { certificateValidation } from '../../../service/yup';
import ButtonSquareLineB from '../../../components/ButtonSquareLineB';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { useEffect, useRef, useState } from 'react';
import FormErrorMessage from '../../../service/FormErrorMessage';


// 파일업로드
// https://velog.io/@yiyb0603/React%EC%97%90%EC%84%9C-%EB%93%9C%EB%9E%98%EA%B7%B8-%EC%95%A4-%EB%93%9C%EB%A1%AD%EC%9D%84-%EC%9D%B4%EC%9A%A9%ED%95%9C-%ED%8C%8C%EC%9D%BC-%EC%97%85%EB%A1%9C%EB%93%9C-%ED%95%98%EA%B8%B0

interface ListsProps {
    clickAddEvent: () => void;
    item: CertificateListArray;
    onEdit: (num: number) => void;
    edit: boolean[];
    clickDeleteEvent: (itemId: number) => void;
    clickUpdateEvent: (itemId: number, data: CertificateListArray) => void;
    clickSaveEvent: () => void;
    addValidId: () => void;
}



const CertificateLists: React.FC<ListsProps> = (props) => {

    const initInput = {
        id: props.item.id,
        title: props.item.title,
        organization: props.item.organization,
        fileName: props.item.fileName,
    }
    const [input, setInput] = useState(initInput);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        console.log("name", name, "value", value);
        setInput({
            ...input,
            [name]: value
        });
        props.clickUpdateEvent(props.item.id, input);
    }
    const { handleSubmit, formState: { errors }, register } = useForm<CertificateListArray>({
        resolver: yupResolver(certificateValidation),
        mode: 'onBlur',
    });
    const onSubmit = handleSubmit((data: CertificateListArray) => {
        console.log("onSubmit >>>", props.item.id, data);
        props.addValidId();
    });


    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                    <div className="common-flex-column-wrap">
                        {
                            props.item.id === 0 &&
                            <>
                                <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                                    <div className="item-lr">
                                        <div className="common-flex-row-wrap-vcenter">
                                            <span className="font-bold-s21-c573300" style={{ marginLeft: 8 }}>자격정보</span>
                                        </div>
                                    </div>
                                    <div className="item-lr">
                                        {
                                            props.edit[0] === false ?
                                                <ButtonSquareLineB type={0} onClick={() => props.onEdit(0)}>편집</ButtonSquareLineB>
                                                :
                                                <ButtonSquareLineB type={1} onClick={() => onSubmit()}>저장</ButtonSquareLineB>
                                        }
                                    </div>
                                </div>
                                <div className="sign-line"></div>
                            </>
                        }


                        <div className="common-flex-row-wrap-vcenter"
                            style={{ marginLeft: 10, marginRight: 10 }}>
                            <img src="/asset/img/myinfo_img_brown_spot.svg" />
                            <div style={{ marginLeft: 10, width: 100 }}>
                                <span className="font-bold-s15-c000000">자격증명</span>
                            </div>
                            <div>
                                <InputBase sx={{ ml: 1, flex: 1 }}
                                    placeholder="Search"
                                    {...register('title')}
                                    value={input.title || ""}
                                    onChange={onChange} />
                                {errors.title && (
                                    <FormErrorMessage errorMessage={errors.title.message!} />
                                )}
                            </div>
                        </div>
                        <div className="sign-line"></div>
                        <div className="common-flex-row-wrap-vcenter"
                            style={{ marginLeft: 10, marginRight: 10 }}>
                            <img src="/asset/img/myinfo_img_brown_spot.svg" />
                            <div style={{ marginLeft: 10, width: 100 }}>
                                <span className="font-bold-s15-c000000">발급기관</span>
                            </div>
                            <div>
                                <InputBase sx={{ ml: 1, flex: 1 }} placeholder="Search"
                                    {...register('organization')}
                                    value={input.organization || ""}
                                    onChange={onChange} />
                            </div>
                        </div>

                        <div className="sign-line"></div>

                        <div className="common-flex-row-wrap-space-between-vcenter" style={{ marginLeft: 10, marginRight: 10 }}>
                            <div className="common-flex-row-wrap-vcenter" >
                                <img src="/asset/img/myinfo_img_brown_spot.svg" />
                                <span className="font-bold-s15-c000000" style={{ marginLeft: 10, width: 100 }}>파일첨부</span>
                                <InputBase sx={{ ml: 1, flex: 1 }} placeholder="Search"
                                    {...register('fileName')}
                                    value={input.fileName || ""}
                                    onChange={onChange} />
                            </div>
                            <div className="common-flex-row-wrap-space-between-vcenter">
                                <div>
                                </div>
                                <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10, marginLeft: 10 }}>
                                    {
                                        props.edit[0] === false ?
                                            <BoxLabel background="#FF9100" marginLeft={10}>굿로드 인증</BoxLabel>
                                            :
                                            <>
                                                <ButtonSquareLineB type={1} >첨부하기</ButtonSquareLineB>
                                                <ButtonSquareLineB type={1} onClick={() => props.clickAddEvent()}>자격추가</ButtonSquareLineB>
                                                {props.item.id !== 0 &&
                                                    <ButtonSquareLineB type={1} onClick={() => props.clickDeleteEvent(props.item.id)}>삭제하기</ButtonSquareLineB>
                                                }
                                            </>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}
export default CertificateLists

