import { LocalizationProvider, DesktopDatePicker } from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { InputBase, FormControlLabel, Checkbox, Box, TextField, InputAdornment } from "@mui/material";
import moment from "moment";
import { useRef, useState } from "react";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import { makeStyles } from "@mui/styles";
import { ChildInfoListArray, CertificateListArray } from "./components/MyInfoType";
import ButtonSquareLineB from "../../components/ButtonSquareLineB";
import ChildInfoLists from "./components/ChildInfoList";

interface localProps {

}

const ChildInfo: React.FC<localProps> = (props) => {



    // ChildInfoList
    const initList = {
        id: 0,
        title: '',
        organization: '',
        fileName: '',
    }
    const [childInfoList, setChildInfoList] = useState<ChildInfoListArray[]>([initList]);
    const nextId = useRef(0);
    const clickAddEvent = () => {
        const id = nextId.current += 1;
        const list = {
            id: nextId.current,
            title: "",
            organization: "",
            fileName: "",
        };
        setChildInfoList(() => childInfoList.concat(list));
    };
    const clickUpdateEvent = (itemId: number, data: ChildInfoListArray) => {
        setChildInfoList(
            childInfoList.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
        console.log("data", data);

    };
    const clickSaveEvent = () => {
        // changeEdit(0, false);
    }
    const clickDeleteEvent = (itemId: number) => {
        setChildInfoList(childInfoList.filter(list => list.id !== itemId));
    };

    return (
        <>
            <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>



                {
                    localStorage.getItem('therapist') === "true" &&
                    <div className="media-container-round-white-shadow-padding40" style={{ marginTop: 10 }}>
                        {childInfoList.map((item, index) => (
                            <ChildInfoLists
                                key={item.id}
                                item={item}
                                clickAddEvent={clickAddEvent}
                                clickDeleteEvent={clickDeleteEvent}
                                clickUpdateEvent={clickUpdateEvent}
                                clickSaveEvent={clickSaveEvent}
                            />
                        ))}
                    </div>
                }

            </div>
            <div className="common-flex-row-reverse-wrap-vcenter" style={{ marginTop: 20 }}>
                <img src="/asset/img/mypage_child_info_img_child_plus_button.svg" onClick={() => clickAddEvent()} />
            </div>

        </>

    )

}
export default ChildInfo


