import { LocalizationProvider, StaticDatePicker, DesktopDatePicker, PickersDay, pickersDayClasses, PickersDayProps } from "@mui/lab";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import { Box, TextField, Avatar, FormControlLabel, Checkbox, InputBase, Modal, RadioGroup, Radio, Input, Badge } from "@mui/material";
import { ko } from "date-fns/locale";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import ButtonSquareLineB from "../../components/ButtonSquareLineB";
import ButtonSquareSmall from "../../components/ButtonSquareSmall";
import { calendarStyle, CareerListArray, CertificateListArray, initialProfile, Profile } from "./components/MyInfoType";
import { yupResolver } from '@hookform/resolvers/yup';
import { profileValidation } from "../../service/yup";
import { useForm } from 'react-hook-form';
import FormErrorMessage from "../../service/FormErrorMessage";
import { addDays, isSameDay } from "date-fns";
import { BoxLabel } from "../../components";
import ChipMedium from "../../components/ChipMedium";
import CertificateLists from "./components/CertificateList";
import DragDrop from "./components/DragDrop";
import ScheduleMemo from "./components/ScheduleMemo";
import MyInfoEditor from "./components/MyInfoEidtor";
import CareerLists from "./components/CareerList";

interface Props {
}
const ParentsInfo: React.FC<Props> = (props) => {
    // common
    moment.locale('ko');
    const [edit, setEdit] = useState([false, false]);
    const changeEdit = (num: number, flag: boolean) => {
        const newArr = [...edit];
        edit.map((item: boolean, index: number) => {
            if (num == index) {
                newArr[index] = flag;
            } else {
                newArr[index] = false;
            }
            setEdit(() => newArr);
        }
        );
    }
    const onEdit = (num: number) => {
        changeEdit(num, true);
    }

    // 달력 
    const [dateValues, setDateValues] = useState<Date | null>(new Date());
    const [calendar, setCalendar] = useState(moment(new Date()).format('YYYY.MM.DD'));
    const handleDateChange = (date: Date | null) => {
        setDateValues(() => date);
        setCalendar(() => moment(date).format('YYYY.MM.DD'));
    };
    const birthday = addDays(new Date(), 3);
    type HighlightedDay = {
        date: Date;
        styles: React.CSSProperties;
    };
    const highlightedDays: HighlightedDay[] = [
        { date: birthday, styles: calendarStyle },
        { date: addDays(new Date(), 6), styles: calendarStyle }
    ];
    const renderWeekPickerDay = (
        date: Date,
        selectedDates: Array<Date | null>,
        pickersDayProps: PickersDayProps<Date>
    ) => {
        const matchedStyles = highlightedDays.reduce((a, v) => {
            return isSameDay(date, v.date) ? v.styles : a;
        }, {});
        return (
            <PickersDay
                {...pickersDayProps}
                sx={{
                    ...matchedStyles,
                    [`&&.${pickersDayClasses.selected}`]: {
                        backgroundColor: "#573300"
                    }
                }}
            />
        );
    };

    // CertificateList
    const initList = {
        id: 0,
        title: '',
        organization: '',
        fileName: '',
    }

    const [certificateList, setCertificateList] = useState<CertificateListArray[]>([initList]);
    const [validId, setValidId] = useState(0);
    const addValidId = () => {
        setValidId(() => (validId + 1));
    }
    const nextId = useRef(0);
    const clickAddEvent = () => {
        const id = nextId.current += 1;
        const list = {
            id: nextId.current,
            title: "",
            organization: "",
            fileName: "",
        };
        setCertificateList(() => certificateList.concat(list));
    };
    const clickUpdateEvent = (itemId: number, data: CertificateListArray) => {
        setCertificateList(
            certificateList.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
        console.log("data", data);

    };
    const clickSaveEvent = () => {
        changeEdit(0, false);
        setValidId(() => 0);
    }
    const clickDeleteEvent = (itemId: number) => {
        setCertificateList(certificateList.filter(list => list.id !== itemId));
    };
    useEffect(() => {
        console.log("certificateList", certificateList);
        console.log("validId", validId);
        if (certificateList.length == validId) {
            console.log("전송");
            clickSaveEvent();
        }
    }, [certificateList, validId])

    // CareerList
    const initCareerList = {
        id: 0,
        title: '',
        organization: '',
        fileName: '',
    }
    const [careerList, setCareerList] = useState<CareerListArray[]>([initCareerList]);
    const nextCareerId = useRef(0);
    const clickCareerAddEvent = () => {
        const id = nextCareerId.current += 1;
        const list = {
            id: nextCareerId.current,
            organization: "",
            fileName: "",
        };
        setCareerList(() => careerList.concat(list));
    };
    const clickCareerUpdateEvent = (itemId: number, data: CareerListArray) => {
        setCareerList(
            careerList.map(
                info => info.id === itemId
                    ? { ...info, ...data }
                    : info
            ));
        console.log("data", data);

    };
    const clickCareerSaveEvent = () => {
        changeEdit(1, false);
    }
    const clickCareerDeleteEvent = (itemId: number) => {
        setCareerList(careerList.filter(list => list.id !== itemId));
    };


    return (
        <>
            <div className="media-grid--col3__12">
                <div className="box-cgc">
                    <div className="media-container-round-white-shadow-padding20">
                        <LocalizationProvider locale={ko} dateAdapter={AdapterDateFns}>
                            <Box
                                sx={{
                                    "& > div": { minWidth: 240 },
                                    "& > div > div, & > div > div > div, & .MuiCalendarPicker-root": { width: 240 },
                                    "& .MuiTypography-caption": { width: 28, margin: 0 },
                                    "& .PrivatePickersSlideTransition-root": { minHeight: 28 * 6 },
                                    '& .PrivatePickersSlideTransition-root [role="row"]': { margin: 0 },
                                    "& .MuiPickersDay-dayWithMargin": { margin: 0 },
                                    "& .MuiPickersDay-root": { width: 28, height: 28 }
                                }}
                            >
                                <StaticDatePicker
                                    displayStaticWrapperAs="desktop" //mobile
                                    openTo="day"
                                    value={dateValues}
                                    onChange={(date) => handleDateChange(date)}
                                    renderInput={(params) => <TextField  {...params} />}
                                    renderDay={renderWeekPickerDay}
                                />
                            </Box>
                        </LocalizationProvider>
                    </div>
                </div>
                <div className="box-cgc">
                    <ScheduleMemo calendar={calendar}></ScheduleMemo>
                </div>
            </div>

            <MyInfoEditor></MyInfoEditor>

            {
                localStorage.getItem('therapist') === "true" &&
                <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>
                    {certificateList.map((item, index) => (
                        <CertificateLists
                            key={item.id}
                            item={item}
                            clickAddEvent={clickAddEvent}
                            onEdit={onEdit}
                            edit={edit}
                            addValidId={addValidId}
                            clickDeleteEvent={clickDeleteEvent}
                            clickUpdateEvent={clickUpdateEvent}
                            clickSaveEvent={clickSaveEvent}
                        />
                    ))}
                </div>
            }

            {
                localStorage.getItem('therapist') === "true" &&
                <div className="media-container-round-grey-shadow-padding40" style={{ marginTop: 10 }}>
                    {careerList.map((item, index) => (
                        <CareerLists
                            key={item.id}
                            item={item}
                            clickCareerAddEvent={clickCareerAddEvent}
                            onEdit={onEdit}
                            edit={edit}
                            clickCareerDeleteEvent={clickCareerDeleteEvent}
                            clickCareerUpdateEvent={clickCareerUpdateEvent}
                            clickCareerSaveEvent={clickCareerSaveEvent}
                        />
                    ))}
                </div>
            }

        </>

    )

}
export default ParentsInfo


