import { Container, Input, Stack, TextField } from "@mui/material"
import { useState } from "react";
import { useNavigate } from "react-router";
import { useRecoilValue } from "recoil";
import { Footer, Header } from "../components"
import ButtonSquare from "../components/ButtonSquare"
import ButtonSquareLarge from "../components/ButtonSquareLarge";
import { countState } from "../service/atom";
import Cookies from 'universal-cookie';
import { Post } from "../api/api";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import FormErrorMessage from "../service/FormErrorMessage";

interface SignInProps {
  onUpdateUser: () => void;
}

const SignIn: React.FC<SignInProps> = (props) => {

  const cookie = new Cookies();
  const linkSignUp = () => {
    window.location.href = "/signup";
  };
  const linkHome = () => {
    window.location.href = "/home";
  };

  type Account = {
    email: string;
    password: string;
  }
  const [account, setAccount] = useState({
    email: "",
    password: "",
  });
  const accountValidation = yup.object({
    email: yup
      .string()
      .required('이메일을 입력해주세요.'),
    password: yup
      .string()
      .required('비밀번호를 입력해주세요.')
      .max(15, '비밀번호는 15자리 이하여야 합니다.')
      .min(4, '비밀번호는 4자리 이상이어야 합니다.'),
  });
  const { handleSubmit, formState: { errors }, register } = useForm<Account>({
    resolver: yupResolver(accountValidation),
    mode: 'onBlur',
  });
  const onSubmit = handleSubmit((data: Account) => {
    console.log("account", data);
    (async () => {
      try {
        const response = await Post.loginPost()
          .then(result => {
            console.log("response : Post.getPosts", result);
            cookie.set('access_token', "second cookie");
            cookie.set("isAuthenticated", "true");
            //linkHome();
          })
          .catch(function (error) {
            if (error.response) {
              alert(error.response.data.message);
            }
          });
      } catch (err) {

      }
    })();
  });
  const onChangeAccount = (e: React.ChangeEvent<HTMLInputElement>) => {
    setAccount({
      ...account,
      [e.target.name]: e.target.value,
    });
  };

  // const onSubmitAccount = async () => {
  //   try {
  //     console.log("account", account);
  //     (async () => {
  //       try {
  //         const response = await Post.loginPost()
  //           .then(result => {
  //             console.log("response : Post.getPosts", result);
  //             cookie.set('access_token', "second cookie");
  //             cookie.set("isAuthenticated", "true");
  //             //linkHome();
  //           })
  //           .catch(function (error) {
  //             if (error.response) {
  //               alert(error.response.data.message);
  //             }
  //           });
  //       } catch (err) {

  //       }
  //     })();

  //   } catch (error) {
  //     window.alert(error);
  //   }
  // };

  const linkSignOut = () => {
    cookie.remove("isAuthenticated");
    window.location.href = "/home";
  };


  return (
    <div className="common-bg-login">
      <Header
        className=""
        goodroad="/asset/img/main_top_logo.svg"
        person="/asset/img/non_member_icon_man.svg"
        bell="/asset/img/main_Icon_awesome-bell.svg"
        kakao="/asset/img/main_icon_message.svg"
        color="font-bold-s12-c573300"
      />
      <div className="media-wrapper">
        <div className="common-wrapper__container"></div>
        <div className="common-wrapper__container">
          {/* ######################### Content Area Begin ######################### */}
          <div className="media-blank-50" ></div>
          <div className="common-row-wrap-hcenter-vstretch" style={{ margin: 10 }}>
            <div className="media-sign-container">
              <div className="media-blank-50" ></div>
              <div className="common-flex-column-wrap-hcenter-vcenter">
                <span className="sign-in-up-good-load">GOOD ROAD</span>
              </div>
              <div className="media-blank-50"></div>
              <form onSubmit={onSubmit}>
                <div className="signin-input" style={{ marginTop: 20 }}>
                  <TextField
                    style={{ marginLeft: 20, marginRight: 20, width: '100%' }}
                    {...register('email')}
                    placeholder="아이디를 입력해주세요"
                    onChange={onChangeAccount}
                    size="small"
                    label=""
                    variant="standard"
                    InputProps={{
                      disableUnderline: true,
                    }}
                  />
                </div>
                <div style={{ paddingLeft: 20 }}>
                  {errors.email && (
                    <FormErrorMessage errorMessage={errors.email.message!} />
                  )}
                </div>
                <div className="signin-input" style={{ marginTop: 20, marginBottom: 20 }}>
                  <TextField
                    style={{ marginLeft: 20, marginRight: 20, width: '100%' }}
                    {...register('password')}
                    type="password"
                    placeholder="비밀번호를 입력해주세요"
                    onChange={onChangeAccount}
                    size="small"
                    label=""
                    variant="standard"
                    InputProps={{
                      disableUnderline: true,
                    }}
                  />
                </div>
                <div style={{ paddingLeft: 20 }}>
                  {errors.password && (
                    <FormErrorMessage errorMessage={errors.password.message!} />
                  )}
                </div>
                <div style={{ paddingLeft: 20, paddingRight: 20 }}>
                  <ButtonSquareLarge onClick={() => onSubmit()}>로그인</ButtonSquareLarge>
                </div>
              </form>
              {/* 회원가입 비밀번호찾기 */}
              <div className="common-flex-row-wrap-space-between-vcenter"
                style={{ marginTop: 20, marginRight: 20, marginBottom: 20, marginLeft: 20 }}>
                <div className="item-lr">
                  <span onClick={() => linkSignUp()} className="font-bold-s12-c573300" style={{ cursor: "pointer" }}>
                    회원가입</span>
                </div>
                <div className="item-lr">
                  <span onClick={() => linkSignOut()} className="font-bold-s12-c573300" style={{ cursor: "pointer" }}>비밀번호찾기</span>
                </div>
              </div>

              <div className="media-sign-bottom-box">
                <div className="common-row-wrap-hcenter-vstretch">
                  <img src="/asset/img/login_naver.svg" style={{ width: 60, margin: 10 }} />
                  <img src="/asset/img/login_kakao.svg" style={{ width: 60, margin: 10 }} />
                  <img src="/asset/img/login_google.svg" style={{ width: 60, margin: 10 }} />
                  <img src="/asset/img/login_twitter.svg" style={{ width: 60, margin: 10 }} />
                </div>
              </div>
            </div>
          </div>
          <div className="media-blank-50"></div>

          {/* ######################### Content Area End   ######################### */}
        </div>
        <div className="common-wrapper__container"></div>
        {/* <Footer></Footer> */}
      </div>
    </div>
  )

}

export default SignIn



