import { Box, Checkbox, FormControlLabel, InputAdornment, InputBase, TextField } from '@mui/material';
import * as React from 'react';
import { BoxLabel } from '../../../components';
import { CertificateListArray, ChildInfoListArray } from './MyInfoType';
import { certificateValidation } from '../../../service/yup';
import ButtonSquareLineB from '../../../components/ButtonSquareLineB';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { useState } from 'react';
import { LocalizationProvider, DesktopDatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import moment from "moment";

interface Props {
    clickAddEvent: () => void;
    item: ChildInfoListArray;
    clickDeleteEvent: (itemId: number) => void;
    clickUpdateEvent: (itemId: number, data: ChildInfoListArray) => void;
    clickSaveEvent: () => void;
}


const ChildInfoLists: React.FC<Props> = (props) => {

    const [edit, setEdit] = useState(false);
    const onEdit = () => {
        setEdit(() => !edit);
    }

    {/* 달력 */ }
    const [values, setValues] = useState<Date | null>(new Date());
    const [calendar, setCalendar] = useState("2021-10-28");
    const handleDateChange = (date: Date | null) => {
        moment.locale('ko');
        setValues(date);
        const tmp = moment(values).format('YYYY-MM-DD');
        setCalendar(tmp);
        console.log("values", tmp);
    };
    const [birth, setBirth] = useState<Date | null>(new Date());


    const initInput = {
        id: props.item.id,
        title: props.item.title,
        organization: props.item.organization,
        fileName: props.item.fileName,
    }
    const [input, setInput] = useState(initInput);
    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        console.log("name", name, "value", value);
        setInput({
            ...input,
            [name]: value
        });
        props.clickUpdateEvent(props.item.id, input);
    }
    const { handleSubmit, formState: { errors }, register } = useForm<ChildInfoListArray>({
        resolver: yupResolver(certificateValidation),
        mode: 'onBlur',
    });
    const onSubmit = handleSubmit((data: ChildInfoListArray) => {
        console.log("ChildInfoListArray", data);
        props.clickSaveEvent()
    });


    return (
        <>
            <form onSubmit={onSubmit}>
                <div className="common-flex-row-reverse-wrap-vcenter">
                    {
                        edit === false ?
                            <ButtonSquareLineB type={0} onClick={() => setEdit(() => true)}>편집</ButtonSquareLineB>
                            :
                            <ButtonSquareLineB type={1} onClick={() => onSubmit()}>저장</ButtonSquareLineB>
                    }
                </div>
                <div className="media-grid--col2__11">
                    <div className="box-cgc">
                        <div className="common-flex-row-wrap-vcenter">
                            <span className="font-bold-s27-c573300" style={{ marginLeft: 20, marginRight: 20 }}>
                                어린이 정보
                            </span>
                            <img src="/asset/img/myinfo_icon_pencil1.svg" style={{ marginLeft: 0 }} />
                        </div>
                        <div className="sign-line"></div>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                            <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 이름*</span></div>
                            <div><InputBase sx={{ ml: 1, flex: 1 }} placeholder="홍길동" /> </div>
                        </div>

                        <div className="sign-line"></div>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                            <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 성별*</span></div>
                            <div>
                                <FormControlLabel control={<Checkbox defaultChecked size="small" />} label="여성" />
                                <FormControlLabel control={<Checkbox defaultChecked size="small" />} label="남성" />
                            </div>
                        </div>
                        <div className="sign-line"></div>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginLeft: 20, marginRight: 20 }}>
                            <div style={{ width: 100 }}><span className="font-bold-s12-c000000">아이 생년월일*</span></div>
                            <LocalizationProvider dateAdapter={AdapterDateFns}>
                                <DesktopDatePicker
                                    label="Custom input"
                                    value={birth}
                                    onChange={(newValue) => {
                                        setBirth(newValue);
                                    }}
                                    renderInput={({ inputRef, inputProps, InputProps }) => (
                                        <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                                            <input ref={inputRef} {...inputProps} style={{ border: "none", flexGrow: 1, height: 30 }} />
                                            {InputProps?.endAdornment}
                                        </Box>
                                    )}
                                />
                            </LocalizationProvider>
                        </div>
                    </div>
                    <div className="box-cgc">
                        <div className="common-flex-row-reverse-wrap-vcenter" style={{ marginRight: 20 }}>
                            <img src="/asset/img/treatment_icon_x.svg" />
                        </div>
                        <div className="common-flex-row-wrap-vcenter">
                            <span className="font-bold-s27-c573300" style={{ marginLeft: 20, marginRight: 20 }}>
                                메모
                            </span>
                        </div>
                        <div style={{ marginTop: 10 }}>

                            <TextField
                                fullWidth
                                id="outlined-multiline-static"
                                label=""
                                multiline
                                rows={10}
                                defaultValue="치료일지"
                                InputProps={{
                                    style: {
                                        paddingLeft: 30,
                                        backgroundColor: '#f9f9f9',
                                    },
                                    startAdornment: (
                                        <InputAdornment position="start" style={{ position: "absolute", top: 27, left: 10 }}>
                                            <img src="/asset/img/treatment_icon_pencil.svg" />
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </div>
                    </div>
                </div>
            </form>
        </>
    )
}
export default ChildInfoLists

